.App {
  margin: 20px auto;
  max-width: 700px;
}

fieldset {
  border: none;
  padding: 0;
  margin-top: 20px;
}

label {
  display: block;
  margin-top: 20px;
  margin-bottom: 10px;
}

.secondaryFilters {
  margin-top: 45px;
}

.a-label {
  display: inline;
}

select {
  width: 100%;
}

button {
  margin-top: 20px;
}

.tribeSelect {
  margin-top: 20px;
}

.tribeSelect label {
  margin-bottom: 10px;
}

.results {
  margin-top: 30px;
}

table {
  text-align: left;
  width: 100%;
}

td {
  padding-right: 15px;
}

td.url-col {
  word-break: break-word;
}